@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:ital,wght@0,100..800;1,100..800&family=Pixelify+Sans:wght@400..700&display=swap);
html, body{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  min-height:100vh;
  background-color: #122F69;
  font-family:'JetBrains Mono';
  color: rgb(212, 212, 212);
  overflow:auto;
}

#card{
  width:80vw;
  display:flex;
  flex-direction: column;
}

#top-rectangle{
  width:100%;
  font-size:120%;
  padding:3% 3% 1% 3%;
  box-sizing: border-box;
  font-weight:700;
  color:white;
}

#bottom-rectangle{
  width:100%;
  font-size:120%;
  padding:2% 3% 3% 3%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  grid-gap:15px;
  gap:15px;
}

.inner-description{
  font-size:80%;
  margin:1% 0 0% 0;
}

a{
  text-decoration: underline;
  color:rgb(255, 255, 255);
  margin:5%;
  margin-left:0;
}

a:hover{
  cursor:pointer;
}

#enigma-body{
  width: 85%;
  aspect-ratio: 1.5;
  border: 5px solid #ffffff;
  background-color: #2f2f2f;
  box-sizing: border-box;
  margin: 0 auto;
}

#keyboard{
  margin-top:1.5%;
}

#top-row, #middle-row, #bottom-row{
  display: flex;
  justify-content: center;
}

.unpressed-keys{
  width:7%;
  margin:0.5%;
  margin-left:0.65%;
  margin-right:0.65%;
}

#lamps{
  margin-top:1.5%;
}

.unlit-lamps{
  width:4.75%;
  margin:0.5%;
  margin-left:0.65%;
  margin-right:0.65%;
}

#rotors-container{
  margin-top:1.5%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.rotor{
  width:15%;
  margin-left:1%;
  margin-right:1%;
}

#rotors{
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
}

#rod{
  position: absolute;
  z-index: 1;
  width:15%;

}

.gears{
  width:8.5%;
}

#temp-styling{
  margin-left:1%;
}

textarea{
  display:inline-block;
  vertical-align: top;
  width:30%;
  resize:none;  
  margin-left:2.5%;
  margin-right:2.5%;
  font-size:110%;
}

#display-area{
  display: flex;
  align-items: center;
  justify-content: center;
}
